import React,{Component} from 'react';
import './body.css'
import Product from './Product.js';


class Body extends Component{
	
	constructor(props){
		super(props)
		this.state = {overlay:"none",featured:[{id:1,name:"Nike Shoes X",price:200},{id:2,name:"Nike Shoes X",price:200},{id:3,name:"Nike Shoes X",price:200},{id:1,name:"Nike Shoes X",price:200},{id:1,name:"Nike Shoes X",price:200},{id:1,name:"Nike Shoes X",price:200},{id:1,name:"Nike Shoes X",price:200},{id:1,name:"Nike Shoes X",price:200}]}
		this.refProf = false;
	}
	
	clickOverlay(){
		this.refProf.setState({display:"none"})
		this.setState({overlay:"none"})
	}
	
	componentDidMount(){
		document.getElementById("root").style.backgroundColor = "#f8f9fa"//f8f9fa
		

	}
	
	clickProduct(){
		this.setState({overlay:"block"})
		this.refProf.setState({display:"block"})
	}
	
	
	renderProductsToHtml(list){
		let html = list.map((item,key) => 
			<div className="product" onClick={() => this.clickProduct()}><img src={require("./img/items/"+item.id+".jpg")}></img><div className="productDetail"><div className="prodName">{item.name}</div><div className="prodPrice">{item.price} EUR</div><div className="itemDesc"></div></div></div>
		)
		return(html)
		
		
		
	}
	
	
	render(){
		return(
			<div className="Body">

				<span className="logoimg">
					<img src={require("./img/training2.jpg")}></img>
				
				</span>

			
				<div className="Featured">
					<div className="featuredBox">
					<h1></h1>
					{this.renderProductsToHtml(this.state.featured)}
					</div>
				</div>
				
				<div className="Categories">
					<h1>CHOOSE SOMETHING SPECIAL</h1><br /><br />
						<div className="category">
							Shoes
							
							<a href={process.env.PUBLIC_URL + '/#/category/shoes'}><img src={require("./img/catShoes.jpg")}></img></a>
						</div>
						<div className="category">
							T-Shirts
							<a href={process.env.PUBLIC_URL + '/#/category/thirts'}><img src={require("./img/catTshirt.jpg")}></img></a>
						</div>
						<div className="category">
							Pants
							<a href={process.env.PUBLIC_URL + '/#/category/pants'}><img src={require("./img/catPants.jpg")}></img></a>
						</div>
				</div>
				
				
				<div id="overlay" style={{display:this.state.overlay}} onClick={() => this.clickOverlay()}></div>
			  <Product cartHook={this.props.cartHook} ref={(cd) => this.refProf = cd}/>
			  
			</div>
		)
		
	}
}

export default Body;
