import React,{Component} from 'react';
import './category.css';
import Product from './Product.js';


class Category extends Component{
	constructor(props){
		super(props)
		this.state = {overlay:"none",category:false,products:[],shoes:[{id:1,name:"Nike Shoes X",price:200},{id:1,name:"Nike Shoes X",price:200},{id:1,name:"Nike Shoes X",price:200},{id:1,name:"Nike Shoes X",price:200},{id:1,name:"Nike Shoes X",price:200},{id:1,name:"Nike Shoes X",price:200},{id:1,name:"Nike Shoes X",price:200},{id:1,name:"Nike Shoes X",price:200}]}
		
	}
	

	
	componentDidMount(){
		console.log(window.location)
		let category = window.location.hash.split("/")
		
		if(category.length >= 3){
			this.state.category = category[2]
			this.setState({category:category[2]})

		}
		
		this.loadProducts()
		
	}
	
	clickProduct(){
		this.setState({overlay:"block"})
		this.refProf.setState({display:"block"})
	}
	
	clickOverlay(){
		this.refProf.setState({display:"none"})
		this.setState({overlay:"none"})
	}
	
	renderProductsToHtml(list){
			let html = list.map((item,key) => 
				<div className="product" onClick={() => this.clickProduct()}><img src={require("./img/items/"+item.id+".jpg")}></img><div className="productDetail"><div className="prodName">{item.name}</div><div className="prodPrice">{item.price} EUR</div></div></div>
			)
			return(html)
		
		
		
		
	}
	
	loadProducts(){
		fetch('http://localhost:3001/customer/getproducts',{method:"post",body:JSON.stringify({category:this.state.category}),headers:{"Content-Type":"application/json"}})
		.then(res => res.json(),err => console.log('error',err))
		.then(res => {
			if(res.response == 1){
				console.log(res)
				this.setState({products:res.products})
				

			}
		})
		
	}
	
	render(){
		return(
			<div className="CategoryTab">
				<div className="logoShoes">
					<img src={require('./img/logoshoes.jpg')}></img>
				</div>
			
				<div className="categoryT" style={{textTransform: 'capitalize'}}>{this.state.category}</div>
				<div className="productlist">
					<br /><br /><br />
					{this.renderProductsToHtml(this.state.products)}
					
					<span className="NavigateL"><img src={require("./img/navigateL.png")}></img></span>
					<span className="NavigateR"><img src={require("./img/navigateR.png")}></img></span>
				</div>
			
			
				<div id="overlay" style={{display:this.state.overlay}} onClick={() => this.clickOverlay()}></div>
			  <Product cartHook={this.props.cartHook} ref={(cd) => this.refProf = cd}/>
			
			</div>
		)
		
		
	}
	
	
}

export default Category;